      .tags__description {
        font-size: rem(20);
        line-height: rem(34);
        color: #9297a2;

        @include xs-block {
          font-size: 22px;
          text-align: center;
          margin-bottom: 60px;
        }
        @include lg-block {
          text-align: left;
          font-size: rem(20);
          line-height: rem(34);
          width: 47%;
        }
        @include xl-block {
          width: rem(540);
        }
      }

      .tags__list {
        display: flex;
        flex-direction: row;
        flex-wrap: wrap;

        @include xs-block {
          width: 100%;
          justify-content: center;
        }
        @include lg-block {
          width: 45%;
          justify-content: flex-start;
        }
        @include xl-block {
          width: rem(540);
        }

        .tag__item {
          font-size: rem(18);
          font-weight: 400;
          letter-spacing: -0.434118px;
          text-align: center;
          margin: 0 rem(30) rem(30) 0;
          background: linear-gradient(
              0deg,
              rgba(255, 255, 255, 0.1),
              rgba(255, 255, 255, 0.1)
            ),
            #000000;
          border-radius: rem(8);
          &:last-child {
            background: none;
          }

          @include xs-block {
            padding: 11px;
            margin: 0 9px 30px 0;
            &:nth-child(2) {
              display: none;
            }
            &:nth-child(3) {
              margin-right: 0px;
            }
            &:nth-child(5) {
              display: none;
            }
          }
          @include sm-block {
            padding: rem(11);
            margin: 0 rem(9) rem(30) 0;

            &:nth-child(2) {
              display: inherit;
            }
            &:nth-child(3) {
              margin-right: 0px;
            }
            &:nth-child(5) {
              display: inherit;
            }
          }
          @include xl-block {
            margin: 0 rem(20) rem(25) 0;
            &:nth-child(1) {
              margin: 0 rem(30) rem(30) 0;
            }
            &:nth-child(2) {
              display: inherit;
            }
            &:nth-child(3) {
              display: inherit;
              margin: 0 0px rem(30) 0;
            }
            &:nth-child(5) {
              display: inherit;
            }
          }
        }
      }
    }

    .btn {
      position: relative;
      background: linear-gradient(0deg, #ffffff, #ffffff);
      border-radius: rem(40);
      width: rem(420);
      height: rem(80);

      @include xs-block {
        height: rem(120);
        width: 100%;
        border-radius: rem(60);
        display: flex;
        justify-content: center;
        align-items: center;
        margin-top: rem(30);
        margin-bottom: rem(160);
      }
      @include sm-block {
        width: rem(420);
        height: rem(80);
      }
      @include lg-block {
        width: 43%;
        height: rem(80);
        margin-top: rem(41);
      }
      @include xl-block {
        width: rem(420);
      }

      a {
        font-size: rem(26);
        display: inline-block;
        color: #000;
        transition: all 0.3s;

        @include xs-block {
          position: static;
          text-align: center;
          word-wrap: break-word;
          line-height: 30px;
          width: 65%;
        }
        @include sm-block {
          line-height: rem(80);
          width: 90%;
          letter-spacing: -0.627059px;
        }

        &:hover {
          color: #0072ff;
          transform: scale(1.1);
          left: 9.5%;
          transition: all 0.3s;
        }
      }
    }

    .footer_xs_pro {
      @include lg(display, none);
      @include xs-block {
        display: flex;
        flex-direction: column;
        align-items: center;
        justify-content: space-between;

        .pro_socials {
          display: flex;
          justify-content: space-between;
          width: rem(185);
          margin-bottom: rem(90);
          img {
            width: 60px;
            height: 60px;
          }
        }
        .pro_footer_text {
          display: flex;
          justify-content: center;
          flex-wrap: wrap;
          width: 100%;
          li {
            margin: 0 3px 30px 0;
            a {
              color: white;
              font-size: 14px;
            }
          }
        }
        .footer_copyright {
          font-size: 12px;
          padding-bottom: 30px;
        }
      }

      @include sm-block {
        .pro_footer_text {
          width: 100%;
          li {
            a {
              font-size: 18px;
            }
          }
        }
        .footer_copyright {
          margin-bottom: 30px;
        }
      }
    }
  }
}

#pro_footer {
  @include lg(display, block);
  @include xs(display, none);
  position: relative;
  color: white;
  .footer_dark {
    .footer_xl {
      position: relative;
      .footer__navigation {
        margin-top: rem(30);
      }
      .footer_copyright {
        margin-bottom: 0;
        padding-bottom: rem(30);
      }
    }
    .footer__socials {
      height: rem(36);
      max-width: rem(210);
      a {
        padding: rem(25);
      }
    }
  }
}
