.faq_title {
    margin: rem(20) 0 rem(37) 0;
    text-align: center;
    font-size: rem(48);

    @include xs-block {
        line-height: rem(48);
    }
    @include lg-block {
        line-height: rem(100);
    }
}

.faq {
    width: rem(660);
    h2 {
        font-size: rem(36);
        margin-top: rem(32);
        margin-bottom: rem(32);
        font-weight: 500;
    }

    h3 {
        font-size: rem(24);
        margin: rem(30) 0;
        font-weight: 400;
    }

    p {
        font-size: rem(18);
        line-height: rem(24);
        font-weight: 300;
    }
    
    a {
        color: #0072FF;
        &:hover {
            text-decoration-line: underline;
        }
    }

    &:last-child {
        margin-bottom: rem(128);
    }
}



