.doc {
  flex-direction: column;
  margin-bottom: rem(120);

  @include xs-block {
    width: 100%;
  }

  .doc__title {
    display: flex;
    flex-direction: column;
   
    @include lg-block {
      margin-top: rem(71);
      align-items: flex-start;
    }

      h1 {
       font-size: rem(48);
       @include xs-block {
        order: 1;
      }
       @include lg-block{
        text-align: left;
        margin-bottom: rem(30);
       }

      }


      .comment { 
        font-size: rem(22);
        line-height: rem(34);
        color: #9297A2;
        font-weight: 300;

        @include xs-block {
          order: 2;
          position: relative;
          top: 0;
          font-size: rem(16);
          line-height: rem(34);
          margin: 0;
        }
        @include lg-block {
          font-size: rem(22);
          margin-bottom: rem(30);
        } 
      }
  }

  .cookie_title {
    display: flex;
    flex-direction: column;

    @include xs-block {

      align-items: center;
    }
    @include lg-block {
      margin-top: rem(71);
      align-items: flex-start;
    }

    h1 {
      font-size: rem(48);
      @include xs-block {
        text-align: center;
        margin-bottom: rem(20);
      }
      @include lg-block {
        text-align: left;
        line-height: rem(54);
      }
     }
     .comment { 
      @include xs-block {
        order: 2;
        font-size: rem(16);
        line-height: rem(34);
        margin: 0;
      }
      @include lg-block {
        font-size: rem(22);
        line-height: rem(34);
        text-align: left;
        margin-bottom: rem(20);
      }
      font-size: rem(22);
      line-height: rem(34);
      color: #9297A2;
      font-weight: 300;
      
    }
     

  }
  
  .doc__text {
    font-size: rem(18);
    line-height: rem(24);
    font-weight: 300;
  }

  p {
    margin: rem(16) 0;
  }

}

.xs {
  @include xs(display, block);
}

.xs_n { 
  @include xs(display, none);
}

