.cookie {
    position: fixed;
    bottom: 0;
    height: 14.8vh;
    width: 100vw;
    background: #FFFFFF;
    

}




.cookie_content {
    height: 100%;
    display: flex;
    flex-direction: row;
    margin: 0 auto;
    align-items: center;

    @include xs-block {
        width: 75%;
    }
    @include xl-block {
        width: rem(1244);
    }

    .text {

        width: 100%;
        color: #000;
        
        line-height: rem(22);
        letter-spacing: -0.337647px;
        margin: 0;

        @include xs-block {
            text-align: left;
            font-size: rem(14);
        }

        @include xl-block {
            font-size: rem(18);
        }

        h4{
            @include xs-block {
                font-weight: 500;
            }
        }

        a {
            color: #0072FF;
            &:hover {
                text-decoration: underline;
            }     
        }

        p {
            margin: 0;
        }
    }
    .cookie_toggle {
        position: relative;
        top: rem(5);

        @include xs-block {
            width: 15%;
        }
        cursor: pointer;
    }
}



