.header {
    display: flex;
    justify-content: space-between;
    align-items: center;
    z-index: 4;
    @include xs-block {
        padding-top: 62px;
        padding-bottom: 71px;
    }
    @include xl-block {
        padding-top: rem(120);
        padding-bottom: 0;
    }
}

.logo {
    position: relative;
    @include size-xs(6);
    @include size-xl(4);

    img {
        max-width: 100%;
        height: rem(63);
    }
    sup {
        position: absolute;
        text-transform: uppercase;
        font-weight: 500;
       
        @include xs-block {
            top: 50px;
        }
        @include sm-block {
            top: 65px;
        }
        @include xl-block {
            top: rem(68);
        }
    }
}


.lang {
    @include xs(display, none);
    @include lg(display, inherit);
    @include lg-block {
        left: -265px;
    }
    @include xl-block {
       left: -17.5%; 
    }

    position: relative;

    .selected_lang {
        @include xs-block{
            width: rem(80);
        }
        @include xl-block{
            width: rem(80);
        }
        background: no-repeat url("../img/svg/Arr_for_select.svg");
        background-size: 25%;
        font-weight: 700;
        font-size: rem(24);
        width: rem(60);
        height: rem(40);
        padding-right: rem(37);
        background-position: rem(50) rem(10);
        cursor: pointer;        
    }



    .conteiner_langs {
        width: rem(170);
        height: rem(256);
        position: absolute;
        background: no-repeat url("../img/svg/modal_with_shadow.svg");
        background-size: cover;
        flex-direction: column;
    
        align-items: center;
        left: rem(-45);
        top: rem(25);
        transition: all 0.3s linear;
   

        .langs_item {
            font-weight: 700;
            font-size: rem(28);
            margin-top: rem(256/4 - 5);
            cursor: pointer;

            &:hover {
                color: #0072FF;
                transition: color 0.3s;
            }          
        }

        input[type="radio"]:checked+label{ display: none; } 
        input[type="radio"] {display: none;}

       
    }
}


.burger_menue {
  position: relative;
  z-index: 1;
  @include xs-block {
        top: 0;
        left: 25px;
}
@include xl-block {
    top: 0;
    left: rem(136);
}

    input {
        display: block;
        width: rem(36);
        height: rem(35);
        position: absolute;
        top: rem(-19);
        left: rem(-60);
        cursor: pointer;
        opacity: 0; 
        z-index: 2;
        @include xs-block {
           position: absolute;
        }

        &:checked ~ span {
            position: fixed;

            z-index: 6;
            opacity: 1;
            transform: rotate(45deg) translate(rem(-24), rem(-1));
            background: #fff;
            @include xs-block {
                top: rem(80);
                right: rem(30);
            }
            @include xl-block {
            top: rem(139);
            right: 12vw;
            }
        }

        &:checked ~ span:nth-last-child(3) {
            opacity: 0;
            transform: rotate(0deg) scale(0.2, 0.2);
        }

        &:checked ~ span:nth-last-child(2){
            transform: rotate(-45deg) translate(rem(-16), rem(-6));
       
        }

        &:checked ~ .burger_menu__links {
            visibility: visible;
            transform: translateX(0);
            transition: transform 0.9s  cubic-bezier(0.07,0.2,0.05,1.0);
        }


    }

    span {
        display: block;
        width: rem(35);
        height: rem(4);
        right: rem(27);
        margin-bottom: rem(10);
        position: absolute; 
        top: rem(-5);
        background: #000;
        border-radius: rem(5); 
        z-index: -1;
        transform-origin: 0px 0px;      
        transition: transform 0.5s 0.2s cubic-bezier(0.77,0.2,0.05,1.0),
                    background 0.5s 0.2s cubic-bezier(0.77,0.2,0.05,1.0),
                    opacity 0.55s 0.2s ease;

        &:nth-child(2) {
            transform: translateY(rem(13));
            transform-origin: 0% 100%;
            
        }

        &:nth-child(3) {
            transform-origin: 0% 100%;
          
        }
        
        &:nth-child(4) {
            transform: translateY(rem(-13));
            transform-origin: 0% 100%;
          
        } 
      }

      .burger_menu__links {
        box-sizing: border-box;
        display: flex;
        flex-direction: column;
        align-items: center;
        justify-content: space-between;
        color: #fff;
        position: fixed;
        top: 0;
        right: 0;
        height: 100vh;
        margin: 0;
        visibility: visible;
        background: linear-gradient(0deg, rgba(0, 0, 0, 0.5), rgba(0, 0, 0, 0.5)), #0C1723;
        transform: translateX(100%);
        transition: transform 0.5s linear;
        padding-bottom: rem(50);
        z-index: 4;

        @include xs-block {
            width: 100%;
        }

        @include lg-block {
            width: 50vw;
        }

        @include xl-block {
            width: 49vw;
        }

        a{
        
            padding: rem(15);
            margin: rem(5);
            color:white;
            @include lg-block {
                margin: 0 rem(5);
            }
        }



        .lang_for_xs {
            @include lg(display, none);

            @include xs-block {
                align-self: flex-start;
                top: 70px;
                left: 150px;
                display: inline-block;
              
                .selected_lang {
                    color:white;
                    height: 30px;
                    font-weight: 700;
                    font-size: 22px;
                    background: no-repeat url("../img/svg/Arr_for_select _white.svg");
                    font-weight: 700;
                    font-size: 28px;
                    padding-right: 5px;
                    background-position: right 13px;
                } 
                .conteiner_langs {
                    left: -150px;
                }
                .langs_item {
                    color: #000;
                }
            }
        }

        .burger_menu__navigation_link {
            position: relative;

            @include xs-block {
                display: flex;
                flex-direction: column;
                align-items: center;
                height: 50%;
                justify-content: space-evenly;
                top: 30px;
                left: 0;
            }

            @include lg-block {
                top: rem(60);
                left: rem(-55);
                height: 55%;
            
            }

            @include xl-block {
                top: rem(83);
                left: rem(-150);
                height: rem(400);
            }



            li {
                text-align: center;
                margin: rem(19) 0;

                a {
                 
                    font-size: rem(22);
                    font-weight: 500;
                    padding: rem(20) rem(20);
                    @include lg-block {
                    
                        height: rem(50); 
                        font-weight: 400;
                    }

                    &:hover {
                        color: #0072FF;
                        transition: color 0.3s;
                    }
                }

                @include xs-block {
                    margin: 0;
                    &:nth-child(3) {
                        display: none;
                    }
                }
                @include lg-block {
                    &:nth-child(3) {
                        margin: 0;
                        display: inline-block;
                    }
                }


            }
        }
      

      .burger_menu__socials {
        height: rem(36);
        display: flex;
        justify-content: space-between;
        bottom: 18.5vh;
        left: 20%;
        position: relative;
        @include xs-block {
        bottom: 0;
        left: 0;
        width: rem(185);

            img {
                height: 60px;
            }
            height: 60px;
        }

        @include lg-block {
            bottom: -105px;
            left: -55px;
            width: 132px;

            img {
                width: 36px;
                height: 36px;
            }
        }

        @include xl-block {
            bottom: rem(-150);
            width: rem(185);
            left: rem(-150);
            width: rem(132);

            img {
                width: rem(36);
                height: rem(36);
            }
        }

        
    
      }

      .burger_menu__foote_text {
          position: relative;
          bottom: rem(69);
          font-size: rem(16);
          font-weight: 300;

          @include xs-block {
              position: relative;
              bottom: 0;
              margin: 0 auto;
              a {
                padding: 0;
              }
            
          }
          @include lg-block {
            left: -55px;
          }

          @include xl-block {
            left: rem(-150);
        }
          a:hover {
              color: #0072FF;
              transition: color 0.3s;
          }
      }
    }
}

.header_xs_hidden {
    @include xs-block {
        display: none;
    }
    @include lg-block {
        display: flex;
    }
}

