#support {
  height: 100vh;
  @include lg-block {
    overflow: hidden;
  }
}

.support {
  p {
    margin: rem(16) 0;
  }
 
  @include xs-block {
    background: none; 
  }

  @include lg-block {
    height: calc(100vh - rem(186.5));
    box-sizing: border-box;
    display: flex;
    flex-direction: column;
    justify-content: space-around;

  }

  @include xl-block {
    justify-content: space-between;
  }

 
      
    .title {
        font-size: rem(48);
        @include lg-block {
          margin-top: rem(120);
        }
      }

    .content {
      display: flex;

      @include xs-block {
        flex-direction: column;
      }
      @include lg-block {
        flex-direction: row;
        justify-content: space-between;
      }
      @include xl-block {
        flex-direction: row;
        justify-content: space-between;
      
      }
    
        .description {
          @include xs-block {
            width: 100%;
          }
          @include lg-block {
            width: 450px
          }
          @include xl-block {
            width: rem(480)
          }
        

            .text {
              color: #000;
              font-weight: 300;

              @include xs-block {
                text-align: left;
                margin-bottom: 120px;
              }

              @include lg-block {
                font-size: rem(16);
                line-height: rem(22);
              }
              @include xl-block {
                font-size: rem(18);
                line-height: rem(24);
              }
                a {
                  color: #0072FF;
                  &:hover {
                      text-decoration-line: underline;
                  }
              }
            }
          }
      
          .viwe {
            @include xs-block {
              margin:0;
              margin-bottom: rem(60);
              width: 100%;
            }
            @include lg-block {
               width: rem(450);
             }
            @include xl-block {
             width: rem(540);
           }

           .bg_smoll {
            position: absolute;
            background: #F2F2F2;
            background-size:  100%;
            width: 100vw;
            height: rem(480);
            z-index: -1;
            left: 0%;
            
            @include xs-block {
              top: 1048px;
            }

            @include sm-block {
              top: 1087px;
            }
            @include md-block {
              top: 990px;
            }

            @include lg-block {
              display: none;
            
            }
           }

            .suport_list {
              display: flex;
              flex-direction: column;
              align-items: center;
              position: relative;
              
             .bg_lg {

               @include lg-block {
                 z-index: -1;
                 position: absolute;
                 width: rem(450);
                 height: rem(662);
                 top:-100px;
                 left: 0;
                 border-radius: rem(60);
                 background: #F2F2F2;
               }
               @include xl-block {
                z-index: -1;
                position: absolute;
                width: rem(540);
                height: rem(862);
                top:rem(-100);
                left: 0;
                border-radius: rem(60);
                background: #F2F2F2;
         
              }
             }

              .item {
                box-sizing: border-box;
                display: flex;
                flex-direction: column;
                justify-content: center;
                height: rem(100);
                border-radius: rem(28);
                margin-bottom: rem(30);
                padding-left: rem(25);
                background: no-repeat url("../img/svg/Suport_link_arr.svg"), #FFFFFF;

                @include xs-block {
                  background-position: 285px center;
                  width: 100%;
                }
                @include sm-block {
                  background-position: 380px center;
                  width: 420px;
                }
                @include lg-block {
                  background-position: 340px center;
                  width: 380px;
                }
                @include xl-block {
                  background-position: rem(380) center;
                  width: rem(420);
                }

                h4 {
                    margin-bottom: rem(10);
                }
                p {
                  margin: 0;
                  font-size: rem(14);
                  line-height: rem(22);
                  color: #9297A2;
                  font-size: 300;
                }
              }
        }
      }
    }
}